<template>
	<list-item :readonly="readonly" @remove="$emit('remove')">
		<div class="form-group" :class="{'has-warning': !mentee}">
			<label class="containing-label">
				{{ menteeLabel }}
				<input type="text" class="form-control"
					:value="mentee" :readonly="readonly"
					@input="$emit('input', {mentee: $event.target.value})" />
			</label>
			<span v-if="!mentee" class="help-block">
				Please enter the mentee / trainee name or remove this list item
			</span>
		</div>
		<div v-if="type !== 'subjectMentorship'"
				class="form-group" :class="{'has-warning': !subject}">
			<label class="containing-label">
				{{ subjectLabel }}
				<input type="text" class="form-control"
					:value="subject" :readonly="readonly"
					@input="$emit('input', {subject: $event.target.value})" />
			</label>
			<span v-if="!subject" class="help-block">
				Please enter the mentorship subject or remove this list item
			</span>
		</div>
	</list-item>
</template>

<script>
import ListItem from './Item.vue';

export default {
	extends: ListItem,
	props: {
		type: {
			type: String,
			required: true,
			validator(type) {
				return [
					'mentorship',
					'subjectMentorship'
				].includes(type);
			}
		},
		mentee: {
			type: String,
			default: ''
		},
		subject: {
			type: String,
			default: ''
		},
		labels: {
			type: Object,
			required: false
		}
	},

	computed: {
		menteeLabel() {
			return (this.labels && this.labels.mentee)
				? this.labels.mentee
				: 'Mentee name';
		},
		subjectLabel() {
			return (this.labels && this.labels.subject)
				? this.labels.subject
				: 'Project / program / mentorship subject'
		}
	},

	components: {
		ListItem
	}
};
</script>



